dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
  font-family: inherit;
}

/* Common styles by Michael*/
body {
  width: 100%;
  margin: 0 auto;
  color: #FCFCFC;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 0.8;
  background-color: #1b1209;
}

.section {
  width: 100%;
  position: relative;
}

.section--dark {
  background-color: #1b1209;
}

.section--light {
  background-color: #FCFCFC;
}

.container {
  max-width: 1250px;
  width: 100%;
  margin: 0 auto;
  padding: 0 25px;
  position: relative;
}

.sprite {
  display: none;
}

img {
  max-width: 100%;
  display: block;
  cursor: pointer;
}

/* blocks */
/* Header styles by Michael*/
.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}

.header__inner {
  display: grid;
  grid-template-columns: repeat(6, 2fr);
}

@media (max-width: 567px) {
  .header__inner {
    grid-template-columns: repeat(3, 4fr);
  }
}

.header-burger {
  width: 50px;
  height: 50px;
  padding: 15px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  position: absolute;
  top: 50%;
  left: 25px;
  z-index: 1;
  transform: translateY(-50%);
  cursor: pointer;
}

@media (max-width: 567px) {
  .header-burger {
    left: auto;
    right: 0;
  }
}

.header-burger__line {
  width: 30px;
  height: 1px;
  position: relative;
  background-color: #b16100;
}

.header-burger__line::before, .header-burger__line::after {
  width: 100%;
  height: 100%;
  content: '';
  position: absolute;
  left: 0;
  z-index: 1;
  background-color: #b16100;
}

.header-burger__line::before {
  top: -8px;
}

.header-burger__line::after {
  top: 8px;
}

.header-title {
  grid-column: 3 / 4;
  color: #FCFCFC;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 1023px) {
  .header-title {
    font-size: 34px;
  }
}

@media (max-width: 767px) {
  .header-title {
    font-size: 32px;
  }
}

@media (max-width: 567px) {
  .header-title {
    grid-column: 2 / 3;
    font-size: 30px;
    text-align: center;
  }
}

.header-nav {
  grid-column: 4 / 6;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}

@media (max-width: 1280px) {
  .header-nav {
    grid-column-end: 7;
  }
}

@media (max-width: 767px) {
  .header-nav {
    justify-content: space-around;
  }
}

@media (max-width: 567px) {
  .header-nav {
    display: none;
  }
}

.header-nav__link {
  color: #FCFCFC;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  opacity: .7;
}

.header-nav__link:focus {
  outline: none;
}

@media (max-width: 1023px) {
  .header-nav__link {
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .header-nav__link {
    font-size: 12px;
  }
}

/* Header styles by Michael*/
.intro .container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}

@media (max-width: 567px) {
  .intro .container {
    padding-top: 50px;
    align-items: flex-start;
  }
}

.intro__inner {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 4fr);
  grid-template-rows: repeat(4, 3fr);
}

@media (max-width: 567px) {
  .intro__inner {
    grid-template-columns: repeat(1, 12fr);
  }
}

.intro__title {
  grid-column: 2 / 4;
  color: #FCFCFC;
  font-family: "Oswald", sans-serif;
  font-size: 100px;
  font-weight: 700;
  color: #BEA67C;
  text-transform: uppercase;
}

@media (max-width: 567px) {
  .intro__title {
    grid-column: 1 / 2;
  }
}

.intro__desc {
  grid-column: 2 / 4;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: nowrap;
  color: #BEA67C;
}

@media (max-width: 567px) {
  .intro__desc {
    grid-column: 1 / 2;
  }
}

.intro__mouse-block {
  grid-column: 2 / 3;
  grid-row: 4 / 5;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: nowrap;
  position: relative;
  cursor: pointer;
}

@media (max-width: 567px) {
  .intro__mouse-block {
    grid-column: 1 / 2;
  }
}

.intro-mouse {
  fill: #FCFCFC;
}

.intro-arrow {
  width: 2px;
  height: 75px;
  position: absolute;
  bottom: -100px;
  left: 31px;
  z-index: 1;
  cursor: pointer;
  background-color: #FCFCFC;
}

.intro-arrow::before, .intro-arrow::after {
  width: 100%;
  height: 10px;
  content: '';
  position: absolute;
  bottom: -1px;
  z-index: 1;
  background-color: #FCFCFC;
}

.intro-arrow::before {
  left: 3px;
  transform: rotate(45deg);
}

.intro-arrow::after {
  right: 3px;
  transform: rotate(-45deg);
}

/* Nav styles by Michael*/
